import template from "./featured-work.html";
import "./featured-work.css"

const featuredWork = document.createElement("div");
featuredWork.className = "featured-work";
featuredWork.innerHTML = template;

document.body.appendChild(featuredWork);

const images = featuredWork.querySelectorAll('.item-img');

images.forEach(img => {
  const loadingOverlay = img.previousElementSibling;
  
  if (img.complete) {
    loadingOverlay.style.opacity = '0';
  } else {
    img.addEventListener('load', () => {
      loadingOverlay.style.opacity = '0';
    });
    
    img.addEventListener('error', () => {
      loadingOverlay.innerHTML = 'Error loading image';
    });
  }
});